import { IdentityClient, Storage } from "@asgardio/oidc-js";
import React from "react";
import ReactDOM from "react-dom";
import App from "./App";
import * as serviceWorker from "./serviceWorker";
import "./index.css";

const auth = IdentityClient.getInstance();
const serverOrigin = "https://dev.portal.asgardio.io/t/brionmario";
const clientHost = "https://asgardio-js-oidc-sdk-sample.herokuapp.com";

// Initialize the client
auth.initialize({
    baseUrls: [ serverOrigin ],
    clientID: "8ArfnSYzSDxDR3cPDFy1lo2r1rEa",
    serverOrigin: serverOrigin,
    signInRedirectURL: clientHost + "/sign-in",
    signOutRedirectURL: clientHost + "/dashboard",
    storage: Storage.WebWorker
});

console.log(origin);

ReactDOM.render(
    <React.StrictMode>
        <App />
    </React.StrictMode>,
    document.getElementById("root")
);

